const KEYBOARD_EVENTS = {
  LEFT: 37,
  UP: 38,
  RIGHT: 39,
  DOWN: 40,
  BACKSPACE: 8,
  ENTER: [13, 32],
  ESC: 27,
  TAB: 9,
};
const KEY_MAP = {
  Escape: "esc",
  Enter: "enter",
  Space: "space",
};
const isTab = (event) => {
  return event?.keyCode === KEYBOARD_EVENTS.TAB && !event?.shiftKey;
};
const isShiftTab = (event) => {
  return event.keyCode === KEYBOARD_EVENTS.TAB && event?.shiftKey;
};

const isCtrlAlt = (event) => {
  return event?.ctrlKey && event?.altKey;
};

const isCtrlAltUp = (event) => {
  return isCtrlAlt(event) && event?.keyCode === KEYBOARD_EVENTS.UP;
};

const isCtrlAltDown = (event) => {
  return isCtrlAlt(event) && event?.keyCode === KEYBOARD_EVENTS.DOWN;
};

const isCtrlAltLeft = (event) => {
  return isCtrlAlt(event) && event?.keyCode === KEYBOARD_EVENTS.LEFT;
};
const isCtrlAltRight = (event) => {
  return isCtrlAlt(event) && event?.keyCode === KEYBOARD_EVENTS.RIGHT;
};

const isTabbed = (event) => {
  return (
    isTab(event) ||
    isShiftTab(event) ||
    isCtrlAltLeft(event) ||
    isCtrlAltRight(event)
  );
};

const INSTALLED_KEY = Symbol("INSTALLED_KEY");

export {
  KEYBOARD_EVENTS,
  KEY_MAP,
  INSTALLED_KEY,
  isTab,
  isTabbed,
  isShiftTab,
  isCtrlAltDown,
  isCtrlAltLeft,
  isCtrlAltRight,
  isCtrlAltUp,
};
